.contact-section {
  padding-top: 6rem;
  padding-bottom: 3rem;
}

.contact-container {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 4rem;
}

/* Contact Info Styles */
.contact-info h2 {
  font-size: 1.8rem;
  margin-bottom: 1.5rem;
  position: relative;
  display: inline-block;
}

.contact-info h2::after {
  content: '';
  position: absolute;
  bottom: -8px;
  left: 0;
  width: 60px;
  height: 3px;
  background-color: var(--primary-color);
}

.contact-intro {
  margin-bottom: 2.5rem;
  line-height: 1.8;
  color: var(--text-color);
  opacity: 0.9;
}

.contact-details {
  margin-bottom: 2.5rem;
}

.contact-item {
  display: flex;
  align-items: flex-start;
  margin-bottom: 1.5rem;
}

.contact-icon {
  width: 50px;
  height: 50px;
  background-color: rgba(52, 152, 219, 0.1);
  color: var(--primary-color);
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.2rem;
  margin-right: 1rem;
  flex-shrink: 0;
}

.contact-text h3 {
  font-size: 1.1rem;
  margin-bottom: 0.3rem;
}

.contact-text p,
.contact-text a {
  color: var(--text-color);
  opacity: 0.8;
  word-break: break-word;
}

.contact-social h3 {
  font-size: 1.2rem;
  margin-bottom: 1rem;
}

.contact-social .social-links {
  display: flex;
  gap: 1rem;
  flex-wrap: wrap;
}

.contact-social .social-links a {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background-color: rgba(0, 0, 0, 0.05);
  color: var(--text-color);
  font-size: 1.2rem;
  transition: var(--transition);
}

.contact-social .social-links a:hover {
  background-color: var(--primary-color);
  color: white;
  transform: translateY(-3px);
}

/* Form Styles */
.contact-form-container {
  background-color: var(--card-bg);
  border-radius: 10px;
  padding: 2.5rem;
  box-shadow: var(--shadow);
}

.form-group {
  margin-bottom: 1.5rem;
}

.form-group label {
  display: block;
  margin-bottom: 0.5rem;
  font-weight: 500;
}

.form-group input,
.form-group textarea {
  width: 100%;
  padding: 0.8rem 1rem;
  border: 1px solid var(--border-color);
  border-radius: 5px;
  background-color: var(--background-color);
  color: var(--text-color);
  font-family: 'Poppins', sans-serif;
  transition: var(--transition);
}

.form-group input:focus,
.form-group textarea:focus {
  outline: none;
  border-color: var(--primary-color);
  box-shadow: 0 0 0 2px rgba(52, 152, 219, 0.2);
}

.form-group textarea {
  min-height: 150px;
  resize: vertical;
}

.submit-btn {
  width: 100%;
  padding: 1rem !important;
  font-size: 1rem;
  margin-top: 1rem;
}

.form-status {
  margin-top: 1.5rem;
  padding: 1rem;
  border-radius: 5px;
  text-align: center;
}

.form-status.success {
  background-color: rgba(46, 204, 113, 0.1);
  color: #27ae60;
  border: 1px solid rgba(46, 204, 113, 0.2);
}

.form-status.error {
  background-color: rgba(231, 76, 60, 0.1);
  color: #e74c3c;
  border: 1px solid rgba(231, 76, 60, 0.2);
}

/* Tablette et plus petit */
@media screen and (max-width: 992px) {
  .contact-container {
    grid-template-columns: 1fr;
    gap: 3rem;
  }
  
  .contact-form-container {
    padding: 2rem;
  }

  .contact-info {
    order: 1; /* Affiche les infos après le formulaire sur tablette */
  }
}

/* Mobile large */
@media screen and (max-width: 768px) {
  .contact-section {
    padding-top: 5rem;
    padding-bottom: 2rem;
  }

  .contact-info h2,
  .contact-social h3 {
    font-size: 1.5rem;
    text-align: center;
  }

  .contact-icon {
    width: 45px;
    height: 45px;
    font-size: 1rem;
  }

  .contact-text h3 {
    font-size: 1rem;
  }

  .contact-text p,
  .contact-text a {
    font-size: 0.9rem;
  }
  
  .contact-social .social-links {
    justify-content: center;
  }
  
  .contact-item {
    justify-content: center;
    text-align: center;
  }
  
  .contact-details {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  .contact-info h2::after {
    left: 50%;
    transform: translateX(-50%);
  }
  
  .contact-intro {
    text-align: center;
  }
}

/* Mobile petit */
@media screen and (max-width: 480px) {
  .contact-container {
    gap: 2rem;
  }
  
  .contact-form-container {
    padding: 1.5rem;
    border-radius: 8px;
  }

  .contact-info h2::after {
    width: 40px;
    height: 2px;
  }
  
  .contact-item {
    flex-direction: column;
    align-items: center;
    margin-bottom: 2rem;
  }
  
  .contact-icon {
    margin-bottom: 0.8rem;
    width: 40px;
    height: 40px;
    margin-right: 0;
  }
  
  .contact-text {
    text-align: center;
  }
  
  .form-group {
    margin-bottom: 1rem;
  }
  
  .form-group label {
    margin-bottom: 0.3rem;
    font-size: 0.9rem;
  }
  
  .form-group input,
  .form-group textarea {
    padding: 0.7rem 0.8rem;
    font-size: 0.95rem;
  }
  
  .submit-btn {
    padding: 0.8rem !important;
  }
}