.footer {
  background-color: var(--card-bg);
  padding: 4rem 0 1.5rem;
  border-top: 1px solid var(--border-color);
}

.footer-container {
  display: flex;
  flex-direction: column;
}

.footer-main {
  display: grid;
  grid-template-columns: 2fr repeat(3, 1fr);
  gap: 2.5rem;
  margin-bottom: 2rem;
}

.footer-branding h2 {
  font-size: 1.8rem;
  font-weight: 600;
  margin-bottom: 1rem;
  color: var(--text-color);
}

.footer-branding span {
  color: var(--primary-color);
}

.footer-branding p {
  color: var(--text-color);
  opacity: 0.8;
  line-height: 1.6;
  max-width: 90%;
  margin-top: 0.75rem;
}

.footer-column h3 {
  font-size: 1.1rem;
  margin-bottom: 1.5rem;
  font-weight: 600;
  color: var(--text-color);
}

.footer-column ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

.footer-column li {
  margin-bottom: 0.75rem;
}

.footer-column a {
  color: var(--text-color);
  opacity: 0.8;
  transition: var(--transition);
  text-decoration: none;
  font-weight: 400;
}

.footer-column a:hover {
  color: var(--primary-color);
  opacity: 1;
}

.social-icons {
  display: flex;
  gap: 1rem;
}

.social-icons a {
  font-size: 1.25rem;
  color: var(--text-color);
  opacity: 0.8;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: var(--transition);
}

.social-icons a:hover {
  color: var(--primary-color);
  opacity: 1;
}

.contact-details p {
  margin: 0 0 0.5rem;
  color: var(--text-color);
  opacity: 0.8;
}

.contact-value {
  margin-top: -0.25rem !important;
  margin-bottom: 1rem !important;
}

.contact-details a {
  color: var(--text-color);
  opacity: 0.8;
  transition: var(--transition);
  text-decoration: none;
}

.contact-details a:hover {
  color: var(--primary-color);
  opacity: 1;
}

.footer-divider {
  height: 1px;
  background-color: var(--border-color);
  margin: 1rem 0 1.5rem;
  opacity: 0.4;
}

.footer-bottom {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
}

.footer-bottom p {
  color: var(--text-color);
  opacity: 0.6;
  font-size: 0.9rem;
  margin: 0;
}

.heart-icon {
  color: #e25555;
  margin: 0 0.25rem;
  animation: heartbeat 1.5s infinite ease-in-out;
}

@keyframes heartbeat {
  0%, 100% { transform: scale(1); }
  50% { transform: scale(1.15); }
}

/* Responsive Styles */
@media screen and (max-width: 992px) {
  .footer-main {
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
  }
  
  .footer-branding {
    grid-column: 1 / -1;
    margin-bottom: 1rem;
  }
}

@media screen and (max-width: 768px) {
  .footer {
    padding: 3rem 0 1.5rem;
  }
  
  .footer-main {
    grid-template-columns: 1fr;
    gap: 2rem;
    text-align: center;
  }
  
  .footer-branding p {
    max-width: 100%;
  }
  
  .footer-column ul {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  .social-icons {
    justify-content: center;
  }
  
  .footer-bottom {
    flex-direction: column;
    gap: 0.75rem;
    text-align: center;
  }
}

/* Mobile Petit */
@media screen and (max-width: 480px) {
  .footer {
    padding: 2.5rem 0 1rem;
  }
  
  .footer-main {
    gap: 1.5rem;
  }
}